<template>
  <div class="page">
    <Header>
      <router-link to="/goodsAnswerMine" slot="right">我的问答</router-link>
    </Header>
    <GoodsInfo :sku="getQuestionInfo.sku"/>
    <div class="answer-detail">
      <div class="answer-title">
        <img src="https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/goods/answer/question.png" alt="">
        <div>
          <h2>{{getQuestionInfo.question}}</h2>
          <p>{{getQuestionInfo.createTime}}</p>
        </div>
      </div>
      <div class="answer-body" v-if="!list.length && finished">
        <div class="answer-item-no">
          <img src="https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/goods/answer/answer_disable.png" alt="">
          <p>暂无回答</p>
        </div>
      </div>
      <div class="answer-body" v-if="list.length">
        <h5><img src="https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/goods/answer/answer.png" alt=""> 共{{list.length}}条回答</h5>
        <van-list
          class="answer-list"
            v-model="loading"
            :finished="finished"
            finished-text="没有更多了"
            @load="onLoad"
          >
          <div class="answer-item" v-for="item in list" :key="item.answerId">
            <img :src="item.headImg || 'https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/goods/answer/user_default.png'" alt="">
            <div class="answer-item-body">
              <div class="answer-user">{{item.nickName}} <span :class="{'invite': item.identity==2}">{{userIdentity[item.identity]}}</span></div>
              <div class="answer-content">{{item.answer}}</div>
              <div class="answer-other">
                <p>{{item.createTime}}</p>
                <div>
                  <span @click="toReplay(item)"><i class="icon-comment"></i>{{item.commentCount}}</span>
                  <span :class="{'active': item.recordId}" @click="doLike(item)"><i class="icon-zan"></i>{{item.likeCount}}</span>
                </div>
              </div>
            </div>
          </div>
        </van-list>
      </div>
      <AnswerEmpty type="1" v-if="!list.length && finished"/>
    </div>
    <div class="answer-add-btn" v-if="isPurchase">
      <router-link :to="`/goodsToAnswer/${getQuestionInfo.questionId}`">我来回答</router-link>
    </div>
  </div>
</template>

<script>
import Header from '@/components/header'
import GoodsInfo from './component/goods-info'
import AnswerEmpty from './component/answer-empty'
import { getPageProductAnswer, getpurchasedGoods, addProductAnswerLikes } from '@/http/service/goods'
import { mapGetters, mapMutations } from 'vuex'

export default {
  name: 'goodsanswerdetail',
  components: {
    Header,
    GoodsInfo,
    AnswerEmpty
  },
  data() {
    return {
      questionId: '',
      curPage: 1,
      pageSize: 20,
      questionInfo: {},
      list: [],
      loading: false,
      finished: false,
      isPurchase: false, // 是否购买过
      userIdentity: {
        1: '已买',
        2: '商家',
        3: '平台'
      }
    }
  },
  activated () {
    this.questionId = this.$route.params.id
    console.log(this.getQuestionInfo, 'getQuestionInfo')
    this.init()
    this.$store.state.isLogin && this.getpurchasedGoods()
  },
  computed: {
    ...mapGetters('answerArea', ['getQuestionInfo']),
    ...mapGetters(['mId']),
  },
  methods: {
    ...mapMutations('answerArea', ['setReplayInfo']),
    init() {
      this.curPage = 1;
      this.list = [];
      this.finished = false;
      this.loading = false
      this.getPageProductAnswer()
    },
    onLoad() {
      this.getPageProductAnswer()
    },
    getpurchasedGoods() {
      getpurchasedGoods({
        answerUserId: this.mId,
        sku: this.getQuestionInfo.sku
      }).then(res => {
        let { success } = res
        if (success) {
          this.isPurchase = true
        }
      })
    },
    getPageProductAnswer() {
      this.loading = true
      let params = {
        start: this.curPage,
        offset: this.pageSize,
        questionId: this.questionId,
      }
      getPageProductAnswer(params).then(res => {
        let { result, success } = res;
        if (success) {
          this.questionInfo =
          this.curPage += 1;
          this.loading = false
          this.list = [...this.list, ...result]
          this.finished = result.length < this.pageSize
        } else {
          this.loading = false
          this.finished = true
        }
      })
    },
    toReplay(item) {
      this.setReplayInfo(item)
      this.$router.push('/goodsToReplay/' + item.answerId)
    },
    doLike(item) {
      // if (item.recordId) return
      addProductAnswerLikes({
        answerId: item.answerId
      }).then(res => {
        let { success } = res;
        if (success) {
          if (item.recordId) {
            item.recordId = false
            item.likeCount -= 1
          } else {
            item.recordId = true
            item.likeCount += 1
          }
        }
      })
    }
  }
}
</script>

